/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable react/jsx-fragments */
/* eslint-disable arrow-body-style */
/* eslint-disable react/jsx-key */
/* eslint-disable no-unused-vars */
/* eslint-disable no-use-before-define */

import React, { useState, useEffect } from 'react';

import { Pagination, Spin, Alert } from 'antd';

import 'antd/dist/antd.css';

import styles from './ArticlesList.module.scss';

import ArticlPreview from '../../ArticlPreview';
import ErrorMessage from '../../ErrorMessage';

import apiService from '../../../services/ApiService';

const ArticlesList = function () {
  const [list, setList] = useState([]); //  
  const [isLoading, setLoading] = useState(true); //  
  const [isError, setIsError] = useState(false); //  
  const [maxPagesNum, setMaxPagesNum] = useState(0); //    
  const [page, setPage] = useState(1); //  

  useEffect(() => {
    getMaxPages(); //   -    
    getList(); //     
  }, []);

  // console.log(page);

  //     
  const getMaxPages = () => {
    apiService
      .getArticlesMax()
      .then((res) => {
        setMaxPagesNum(Math.ceil(res / 5));
        setIsError(false);
      })
      .catch((err) => {
        showError();
      });
  };

  //  
  const getList = () => {
    apiService
      .getArticles()
      .then((newList) => {
        setList(newList);
        setLoading(false);
        setIsError(false);
      })
      .catch((err) => {
        showError();
      });
  };

  //        
  const onChangePage = (value) => {
    setPage(value);
    setList({});
    setLoading(true);
    setIsError(false);

    apiService
      .getArticlesByPageNum(value - 1)
      .then((newList) => {
        setList(newList);
        setLoading(false);
        setIsError(false);
      })
      .catch((err) => {
        showError();
      });
  };

  const showError = () => {
    setIsError(true);
    setList({});
    setLoading(false);
  };

  //       
  const newList =
    list.length && !isError ? (
      <ul className={styles['articles-list']}>
        {list.map((el, i) => {
          return (
            <li>
              <ArticlPreview item={el} key={el.slug} />
            </li>
          );
        })}
      </ul>
    ) : null;

  // 
  const errorMasege = isError ? (
    <ErrorMessage description="Data loading error. Please try reloading the page or try again later." type="error" />
  ) : null;

  //       
  const pagination =
    list.length && !isError ? (
      <Pagination
        className={styles['ant-pagination']}
        showQuickJumper
        showSizeChanger={false}
        defaultCurrent={page}
        total={maxPagesNum * 10}
        onChange={onChangePage}
      />
    ) : null;

  //   
  const loading = isLoading ? <Spin className={styles['ant-spin']} size="large" /> : null;

  return (
    <React.Fragment>
      {newList}
      {pagination}
      {loading}
      {errorMasege}
    </React.Fragment>
  );
};

export default ArticlesList;
